import React, { useContext } from "react";
import {
  Box,
  Container,
  Card,
  CardMedia,
  Grid,
  Input,
  makeStyles,
  Select,
  MenuItem,
  Typography,
  CardContent,
  Button,
  IconButton,
  CircularProgress,
  Paper,
  TextField,
  Chip,
  Divider,
  List,
  ListItem,
  ListItemText,
} from "@material-ui/core";
import { AttachFileRounded, CloudDownloadRounded } from "@material-ui/icons";
import DashboardWrapper from "../components/layout/Wrapper/DashboardWrapper";
import { useParams, Redirect } from "react-router-dom";
import Axios from "axios";
import { DataGrid } from "@material-ui/data-grid";
import { Skeleton } from "@material-ui/lab";
import AuthContext from "../components/context/auth/authContext";
import Dashboard from "../components/routing/Dashboard";

const role = "society";

const useStyle = makeStyles((theme) => ({
  chips: {
    "& > *": {
      margin: theme.spacing(0.5),
    },
  },
  input: {
    display: "none",
  },
}));

const titleCase = (string) =>
  string[0].toUpperCase() + string.slice(1).toLowerCase();

const SecondaryItem = (props) => {
  const { type, value } = props;
  switch (type) {
    case "text":
      return (
        <Typography variant="subtitle1" color="initial">
          {value}
        </Typography>
      );

    case "date":
      return (
        <Typography variant="subtitle1" color="initial">
          {new Date(value).toLocaleDateString()}
        </Typography>
      );
    case "file":
      return (
        <Box py={2}>
          <Button
            variant="outlined"
            color="primary"
            startIcon={<CloudDownloadRounded />}
            component="a"
            href={value}
            target="_blank"
          >
            View file
          </Button>
        </Box>
      );
    case "table":
      return (
        <Box height={400} py={2}>
          <DataGrid
            rows={value.map((val, index) => {
              return { ...val, id: index };
            })}
            columns={Object.keys(value[0]).map((key) => {
              return { field: key, headerName: titleCase(key), width: 200 };
            })}
          />
        </Box>
      );
    default:
      return <React.Fragment />;
  }
};

export const Form = (props) => {
  const { content } = props;

  if (content.length === 0)
    return (
      <Box py={5}>
        <Typography variant="h3" color="initial">
          No Form Content
        </Typography>
      </Box>
    );

  return (
    <List>
      {content.map((item, index) => {
        if (item.value) {
          return (
            <React.Fragment key={index}>
              <Divider />
              <ListItem>
                <ListItemText
                  primary={
                    <Typography variant="body2" color="initial">
                      {item.label}
                    </Typography>
                  }
                  secondary={<SecondaryItem {...item} />}
                />
              </ListItem>
            </React.Fragment>
          );
        }
      })}
    </List>
  );
};

export const Comment = (props) => {
  const { comments } = props;
  const { user } = useContext(AuthContext);
  const title = (comment) => {
    if (comment.sender._id === user._id) {
      return "Me";
    } else if (
      comment.sender.role === "admin" ||
      comment.sender.role === "superAdmin"
    ) {
      return "Admin";
    } else {
      return comment.sender.name;
    }
  };
  if (!comments || comments.length === 0)
    return (
      <Box p={5}>
        <Typography variant="subtitle2" color="initial">
          No Comments available
        </Typography>
      </Box>
    );
  return (
    <Box>
      <List>
        {comments.map((comment, index) => (
          <ListItem key={index}>
            <ListItemText
              primary={
                <Box display="flex" color="gray" alignItems="baseline">
                  <Typography variant="subtitle1" color="primary" gutterBottom>
                    {title(comment)}
                  </Typography>
                  <Box px={2} />
                  {comment.dateCreated && (
                    <Typography variant="body2" color="inherit" gutterBottom>
                      - {new Date(comment.dateCreated).toLocaleDateString()}
                    </Typography>
                  )}
                </Box>
              }
              secondary={
                <Typography variant="body1" color="initial">
                  {comment.text}
                </Typography>
              }
            />
          </ListItem>
        ))}
      </List>
    </Box>
  );
};

const Requests = () => {
  const page = Dashboard();
  const authContext = useContext(AuthContext);

  const classes = useStyle();

  const [files, setFiles] = React.useState([]);
  const [content, setContent] = React.useState([]);
  const [comments, setComments] = React.useState([]);
  const [title, setTitle] = React.useState("");
  const [description, setDescription] = React.useState("");
  const [loading, setLoading] = React.useState(true);
  const [sendingComment, setSendingComment] = React.useState(false);
  const [commentText, setCommentText] = React.useState("");
  const [status, setStatus] = React.useState(null);

  const [dateCreated, setDateCreated] = React.useState(null);

  const { id } = useParams();
  // Text date table file

  const getRequest = async () => {
    setLoading(true);
    try {
      const res = await Axios.get(`/api/requests/${id}`);
      setContent(res.data.request.content);
      setTitle(res.data.request.title);
      setDescription(res.data.request.description);
      setComments(res.data.comments);
      setDateCreated(res.data.request.dateCreated);
      setStatus(res.data.request.status);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  const sendComment = async () => {
    setSendingComment(true);
    try {
      const res = await Axios.post(`/api/requests/comment/${id}`, {
        text: commentText,
      });
      setComments(res.data.comments);
      setCommentText("");
    } catch (err) {
      console.log(err);
    }
    setSendingComment(false);
  };

  React.useEffect(() => {
    getRequest();
  }, []);

  if (authContext.userRole && authContext.userRole !== role)
    return <Redirect to={page} />;
  if (loading)
    return (
      <DashboardWrapper>
        <Box
          height={1}
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <CircularProgress />
          <Typography variant="subtitle1" color="initial">
            Loading...
          </Typography>
        </Box>
      </DashboardWrapper>
    );

  return (
    <DashboardWrapper>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Box p={2}>
            <Typography variant="h4" color="initial" gutterBottom>
              {title}
            </Typography>
            <Box display="flex" alignItems="baseline" color="gray">
              <Typography variant="subtitle1" color="textPrimary" gutterBottom>
                {description}
              </Typography>
              <Box width={10} />
              {dateCreated && (
                <Typography variant="body2" color="inherit">
                  - {new Date(dateCreated).toDateString()}
                </Typography>
              )}{" "}
              <Box width={10} />
              {status && <Chip label={status} color="primary" size="small" />}
            </Box>
            <Form content={content} />
          </Box>
        </Grid>
        <Grid item xs={12} md={4}>
          <Box>
            <Paper>
              <Box p={5} pb={0}>
                <TextField
                  label="Comment"
                  variant="outlined"
                  fullWidth
                  value={commentText}
                  onChange={(e) => setCommentText(e.target.value)}
                />
                <Box pt={1} display="flex" justifyContent="flex-end">
                  <Button
                    variant="contained"
                    color="primary"
                    disabled={sendingComment}
                    onClick={sendComment}
                  >
                    Send
                  </Button>
                </Box>
              </Box>
              <Box py={3}>
                <Divider />
              </Box>
              <Comment comments={comments} />
            </Paper>
          </Box>
        </Grid>
      </Grid>
    </DashboardWrapper>
  );
};

export default Requests;
