import React, { useContext, useEffect } from "react";
import PageWrapper from "../../components/layout/Wrapper/PageWrapper";
import {
  Box,
  Container,
  Typography,
  IconButton,
  Button,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { ArrowBackRounded, CloudDownloadRounded } from "@material-ui/icons";
import fileContext from "../../components/context/files/fileContext";
import { DataGrid } from "@material-ui/data-grid";
import UploadResources from "../UploadResources";

const Elearning = () => {
  const { getResources, resources } = useContext(fileContext);
  useEffect(() => {
    getResources();
  }, []);
  return (
    <PageWrapper>
      <Box py={5}>
        <Container maxWidth="md">
          <Box display="flex" flexDirection="row">
            <Box width={1} display="flex">
              <IconButton
                style={{ marginLeft: 20, height: 50, width: 50 }}
                component={Link}
                to="/admin"
              >
                <ArrowBackRounded />
              </IconButton>
              <Typography variant="h3" color="initial" gutterBottom noWrap>
                E-Learning
              </Typography>
            </Box>
            <Box
              width={1}
              display="flex"
              justifyContent="flex-end"
              alignItems="center"
            >
              <UploadResources type="E-learning" />
            </Box>
          </Box>
          <Box height={400}>
            <DataGrid
              rows={resources
                .filter((file) => file.type === "E-learning")
                .map((resource, index) => {
                  return {
                    ...resource,
                    id: resource._id,
                  };
                })}
              columns={[
                { field: "title", headerName: "Title", width: 250 },
                {
                  field: "details",
                  headerName: "Description",
                  width: 400,
                },
                {
                  field: "dateUploaded",
                  headerName: "Date Uploaded",
                  width: 120,
                  valueGetter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
                {
                  field: "url",
                  headerName: "Download",
                  width: 100,
                  renderCell: (params) => (
                    <IconButton
                      component="a"
                      color="primary"
                      href={params.value}
                      target="_blank"
                    >
                      <CloudDownloadRounded />
                    </IconButton>
                  ),
                },
              ]}
            />
          </Box>
        </Container>
      </Box>
    </PageWrapper>
  );
};

export default Elearning;
